.card {
  height: 510px;
  color: #939393;
  max-width: 320px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: 400;
}

.card__border {
  height: 480px;
  box-sizing: border-box;
  width: 320px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  cursor: pointer;
}

.card__border_type_default {
  background: linear-gradient(135deg, transparent 30px, #1698d9 0);
}

.card__border_type_default-hover {
  background: linear-gradient(135deg, transparent 30px, #2ea8e6 0);
}

.card__border_type_disabled {
  background: linear-gradient(135deg, transparent 30px, #b3b3b3 0);
}

.card__border_type_disabled:hover {
  cursor: auto;
  background: linear-gradient(135deg, transparent 30px, #b3b3b3 0);
}

.card__border_type_selected {
  background: linear-gradient(135deg, transparent 30px, #d91667 0);
}

.card__border_type_selected-hover {
  background: linear-gradient(135deg, transparent 30px, #e62e7a 0);
}

.card__container {
  height: 472px;
  box-sizing: border-box;
  width: 312px;
  background: linear-gradient(135deg, transparent 30px, #f2f2f2 0);
  border-radius: 7px;
  overflow: hidden;
  position: relative;
}

.card_disaibled {
  position: absolute;
  top:0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #b3b3b3;
  background: linear-gradient(135deg, transparent 30px, #f2f2f2 0);
  z-index: 8;
  opacity: 0.5;
}

.card__box-info {
  padding: 18px 20px 0 43px;
}

.card__subtitle {
  margin: 0;
  font-size: 16px;
}

.card__title {
  color: #000;
  margin: 16px 0 0;
  font-weight: 700;
  font-size: 48px;
  line-height: 0.7;
}

.card__title_disabled {
  color: #b3b3b3;
}

.card__data {
  margin-top: 20px;
}

.card__title_type_product {
  font-size: 24px;
}

.card__info {
  margin: 0;
  font-size: 14px;
}

.card__info_type_weight {
  font-weight: 700;
}

.card__img {
  width: 370px;
  height: 361px;
  position: absolute;
  left: -28px;
  top: 203px;
}

.card__signature {
  color: #FFF;
  margin: 15px 0 0;
  font-size: 13px;
  font-weight: 500;
}

.card__signature_type_sold {
  color: #ffff66;
}

.card__signature_type_buy {
  color: #1698d9;
  border-bottom: 1px dashed #1698d9;
  cursor: pointer;
}

.card__box-weight {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  position: absolute;
  bottom: 11px;
  right: 11px;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 10;
}

.card__box-weight_type_default {
  background-color: #1698d9;
}

.card__box-weight_type_default-hover {
  background-color: #2ea8e6;
}

.card__box-weight_type_selected {
  background-color: #d91667;
}

.card__box-weight_type_selected-hover {
  background-color: #e62e7a;
}

.card__box-weight_type_disabled {
  background-color: #b3b3b3;
}

.card__weight {
  margin: 0;
  color: #f2f2f2;
  font-size: 21px;
}

.card__weight_type_number {
  font-size: 42px;
  line-height: 0.8;
  padding-top: 17px;
}

@media screen and (max-width: 970px) {
  .card {
    margin-right: 30px;
    margin-bottom: 30px;
  }

  .card:nth-of-type(2n) {
    margin-right: 0;
  }
}

@media screen and (max-width: 650px) {
  .card {
    margin-right: 0;
  }
}
